.contacts_page {
  .map {
    height:     auto;
    text-align: center;
    iframe {
      width: 100%;
    }
  }
  .arrow-down {
    background:    url("../images/line_angle_middle.png") no-repeat center;
    background-size: 100% 100%;
    width:         100%;
    height:        14px;
    margin-bottom: 40px;
  }
  h1 {
    color:       #393b3d;
    padding-bottom: 40px;
    font-weight: 300;
    font-size:   28px;
    line-height: 17px;
    font-family: $ProximaNova;
  }
  hr {
    padding:    0;
    border:     none;
    text-align: center;
    position:   relative;
  }
  hr:after {
    content:       '';
    position:      absolute;
    top:           0;
    display:       inline-block;
    border:        10px solid #F3F3F3;
    border-bottom: 10px solid transparent;
    border-top:    10px solid $green;
  }
  hr:before {
    content:    '';
    position:   absolute;
    display:    inline-block;
    top:        -1px;
    z-index:    1;
    border:     10px solid transparent;
    border-top: 10px solid #F3F3F3;
  }
  .our-contacts {
    padding: 0;
    margin-bottom: 234px;
    padding-top: 54px;
    .address_icon_wr,
    .mobile_icon_wr,
    .email_icon_wr {
      height: 50px;
      i {
        font-size:      28px;
        color:          $green;
        vertical-align: top;
        margin-right:   20px;
      }
    }
  }
  .address_content,
  .mobile_content,
  .email_content {
    font-family: $ProximaNova;
    p {
      color:         #292c2e;
      font-weight:   300;
      font-size:     20px;
      line-height:   26px;
      margin-bottom: 0;
    }
  }
  section.our-contacts {
    margin-bottom: 234px;
  }
}