#ui-datepicker-div {
	margin-top: 11px;
	margin-left: -2px;
	border: 0;
	padding: 0;
	td span,
	td a {
		text-align: center;
		background: transparent;
		border: 0;
		padding: 7px;
	}
	.ui-datepicker-header {
		border-radius: 0;
		background: $green;
		border: 0;
		padding: .5em;
		color: #fff;
		font-family: $ProximaNova;
		.ui-datepicker-prev,
		.ui-datepicker-next {
			top: 7px;
			&:hover {
				background: transparent;
				border: none;
			}
			.ui-icon {
				color: #fff;
			}
		}
	}
	.dayFrom,
	.dayEnd,
	.ui-state-hover {
		background: $green;
	}
	.select-range-days {
		background: #ccc !important;
	}
	.active-days {
		background: #f1f1f1;
	}
	.firstDay {
		background: $green !important;
	}
	.endDay {
		background: $green;
	}
}

