.product_catalog {
	.search-currency {
		display: inline-block;
		float: right;
		a {
			color: #676767;
			font-weight: 300;
			font-size: 12px;
			line-height: 36px;
			font-family: $ProximaNova;
		}
	}
	.block_title {
		a {
			--bloc_title_color: #727274;
			color: var(--bloc_title_color);
			border-bottom: 1px dashed var(--bloc_title_color);
		}
	}
	.lens {
		font-family: 'HelveticaNeueCyr', serif;
		color: #075bb3;
		font-size: 14px; /*  -   */
		line-height: 20px; /*  -   */
		text-align: center;

		img {
			width: 50px;
			height: 50px;
			margin-right: 15px;
		}
		.border {
			width: 262px;
			display: inline-block;
			padding: 13px 11px;
			border: 1px solid #f2f2f2;
			margin-bottom: 30px;
			text-align: left;
		}
	}
	.block_title {
		margin-bottom: 30px;
	}
	ul.nav.nav-tabs li {
		border-bottom: 2px solid #DDDADA;
		&.active {
			border-color: $green;
			position: relative;
			a:after,
			a:hover:after,
			a:active:after {
				content: '';
				position: absolute;
				bottom: 0;
				z-index: 1;
				border: 4px solid transparent;
				border-bottom: 5px solid $green;
				left: calc(50% - 3px);
			}
		}
		a {
			background: transparent;
			border: 0;
			padding-bottom: 15px;
			font-family: $ProximaNova;
			color: #717171;
			font-size: 18px;
			font-weight: 300;
			line-height: 16px;
		}
	}
	.catalog {
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;

		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		&.row > [class*=col-] {
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			margin-bottom: 30px;
		}
		.hotel-item {
			position: relative;
			height: 100%;
			border-left: 1px solid #DDDDDD;
			border-right: 1px solid #DDDDDD;
			border-bottom: 1px solid #DDDDDD;
			--color-price: $green;
			background: #fff;
			.hotel-content {
				padding-bottom: 75px;
				.hotel-name,
				.distance-info,
				.hotel-stars {
					padding-left: 10px;
				}
				.hotel-name {
					color: #3a3b3d;
					font-size: 16px;
					line-height: 30px;
					font-family: $ProximaNova;
				}
				.distance-info {
					position: relative;
					color: #636365;
					font-size: 12px;
					line-height: 18px;
					display: inline-block;
					p {
						margin-bottom: 0;
					}
					i {
						position: absolute;
						right: -20%;
						top: 30%;
						color: var(--color-price);
						font-size: 18px;
					}
					margin-bottom: 11px;
				}
				.hotel-wr-price {
					border-bottom: 2px solid var(--color-price);
					.hotel-price {
						background: var(--color-price);
						margin-bottom: 2px;
						padding: 5px;
						color: #fff;
						text-align: center;
						padding-top: 13px;
						padding-bottom: 6px;
						p {
							font-size: 20px;
							margin-bottom: 0;
						}
						span {
							font-size: 14px;
						}
					}
				}
				.hotel-stars {
					line-height: 26px;
					i {
						color: #FDAD00;
						margin-right: 4px;
						font-size: 16px;
					}
				}
				.rating_wrap {
					float: right;
					padding: 0 10px 10px 10px;
					margin: 0;
					.text_rating {
						color: #616563;
						font-size: 18px;
						line-height: 26px;
						margin-right: 11px;
					}
					.rating {
						color: #ff9108;
						font-size: 26px;
						line-height: 26px;
						margin-right: 8px;
					}
					.like {
						border: none;
						background: transparent;
						padding: 0;
						outline: none;
						i {
							font-size: 22px;
							color: #ADADAD;
							line-height: 26px;
						}
					}
				}
				.wr-button-submit {
					position: absolute;
					bottom: 15px;
					width: 100%;
				}
				button.btn-look {
					border: 1px solid var(--color-price);
					background: transparent;
					border-radius: 30px;
					padding: 12px 20px;
					color: #07af61;
					font-size: 16px;
				}
			}
		}
	}
}

