.wr-slider {
	position: relative;
}

/* sliders */
.concerts-tur,
.recreation-tur,
.europe-tur,
.news,
.shares {
	font-family: $ProximaNova;
	figure {
		position: relative;
		margin-top: 5px;
		margin-bottom: 0;
		&:hover {
			-webkit-transition: all 0.2s ease-out 0.1s;
			-moz-transition: all 0.2s ease-out 0.1s;
			-o-transition: all 0.2s ease-out 0.1s;
			transition: all 0.2s ease-out 0.1s;
			box-shadow: -1px 7px 7px 0 #ccc;
			margin-top: 0;
			margin-bottom: 5px;
		}
	}
	.owl-item {
		padding-left: 15px;
		padding-right: 15px;
	}
}

.concerts-tur,
.recreation-tur,
.europe-tur {
	figure {
		.top-corner {
			position: absolute;
			top: 4px;
			right: 6px;
			z-index: 1;
			background: url("../images/plashka.png") no-repeat;
			width: 139px;
			height: 131px;
			color: #fff;
			padding-top: 15px;
			p {
				text-align: center;
				font-size: 42px;
				line-height: 42px;
				font-family: 'Roboto', sans-serif;
				margin: 0;
				padding: 0;
				span {
					font-size: 22px;
					line-height: 30px;
				}
			}
			hr + p {
				font-size: 24px;
			}
			hr {
				margin-top: 5px;
				margin-bottom: 5px;
				border: 0;
				height: 1px;
				color: #fff;
				background: #fff;
				width: 70%;
			}
		}
		img {
			width: 100%;
		}
	}
	.owl-item {
		figcaption {
			background: #FDFDFD;
			padding: 29px;
			color: #3a3b3d;
			font-family: 'ProximaNova', sans-serif;
			h3 {
				font-size: 26px;
				line-height: 32px;
				margin-bottom: 25px;
			}
			p {
				font-weight: 300;
				font-size: 20px;
				line-height: 26px;
			}
		}
	}
}

.owl-stage {
	.owl-item .item-slide {
		figcaption {
			height: 270px;
		}
	}
}

.news figcaption {
	position: relative;
	&:before {
		position: absolute;
		top: -25px;
		left: 42%;
		content: '';
		border-left: 25px solid transparent;
		border-right: 25px solid transparent;
		border-bottom: 25px solid #fff;
	}
}

.news,
.shares {
	.owl-item {
		figcaption {
			background: #FDFDFD;
			padding: 20px;
			h3 {
				color: #3a3b3d;
				font-size: 22px;
				line-height: 36px;
				text-align: center;
				height: 65px;
			}
			time {
				display: block;
				text-align: right;
				color: #6b6a6a;
				font-weight: 300;
				font-size: 14px;
				line-height: 38px;
			}
			p {
				text-align: center;
				color: #3a3b3d;
				font-weight: 300;
				font-size: 18px;
				line-height: 24px;
			}
			.wr-button {
				text-align: center;
				a,
				a:hover,
				a:active {
					margin: 0;
					color: #07af61;
					font-size: 24px;
					line-height: 38px;
					span {
						margin-left: 5px;
					}
				}
			}
		}
	}
}

.shares {
	.owl-item {
		.wr-img {
			position: relative;
			.percent {
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				text-align: center;
				margin: 0 auto;
				height: 93px;
				background: #e63e0a;
				width: 50%;
				color: #ffffff;
				font-size: 60px;
				line-height: 93px;
			}
		}
		figcaption {
			padding: 0 0 20px;
			h3 {
				background: #FD8D00;
				height: 96px;
				line-height: 96px;
				color: #ffffff;
				font-size: 27px;
			}
			p {
				padding-top: 30px;
				color: #3a3b3d;
				font-size: 28px;
				line-height: 32px;
			}
			time {
				text-align: center;
				color: #07af61;
				font-size: 30px;
				line-height: 38px;
				span {
					color: #3a3b3d;
					font-size: 21px;
				}
			}
			p, time {
				padding-left: 20px;
				padding-right: 20px;
			}
		}
	}
}

/* end slider tur */
/* navigation */
.home .owl-stage-outer {
	margin-bottom: 58px;
}

.owl-prev,
.owl-next {
	position: absolute;
	top: 35%;
}

.owl-prev {
	left: -50px;
}

.owl-next {
	right: -50px;
}

/* dots */
.home .owl-dots {
	text-align: center;
	.owl-dot,
	& span {
		width: 50px;
		height: 4px;
		display: inline-block;
	}
	.owl-dot {
		margin: 0 5px;
	}
	.owl-dot span {
		color: $grey_color;
		background: $grey_color;
	}

	.owl-dot.active span,
	.owl-dot:hover span {
		color: $green;
		background: $green;
	}
}

/*booking page */
.booking_page {
	.concerts-tur .owl-item, .recreation-tur .owl-item, .europe-tur .owl-item, .news .owl-item, .shares .owl-item {
		padding: 0;
	}
}

/* end navigation */

#moreSharesSlider {
	figure {
		border: 1px solid #BDBDBD;
		figcaption {
			position: relative;
			.wr-percent {
				position: absolute;
				top: -92px;
				width: 100%;
				.percent {
					width: 50%;
					margin: 0 auto;
					text-align: center;
					height: 92px;
					line-height: 92px;
					background: #FA430B;
					color: #ffffff;
					font-size: 48px;
					font-family: $ProximaNova;
				}
			}
		}
		.content-items {
			padding: 20px;
			p {
				padding-top: 0;
				font-size: 22px;
				margin-bottom: 0;
			}
			time {
				font-size: 24px;
			}
		}
	}
}