/*==========  Desktop First Method  ==========*/

@media (max-width: 1280px) {
  /*booking_page*/
  .block-free-date .ui-datepicker-multi-2 .ui-datepicker-group {
    width:  60%;
    margin: 0 auto;
    float:  none;
  }
  .block-free-date .ui-datepicker .ui-datepicker-prev {
    left: -31px;
  }
  .block-free-date .ui-datepicker .ui-datepicker-next {
    right: -31px;
  }
}

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  div.subscribe .orang {
    right: 0;
  }
  fieldset.group-radio-conditions {
    margin-bottom: 20px;
  }
  .group-guests .form-group,
  .add_purpose .form-group {
    margin-bottom: 6px;
  }
  .add_purpose {
    margin-top: 10px;
  }
  .search-form .sk-form-item .sk-form-item-inner {
    border-radius: 5px;
  }
  .logo {
    width: 45%;
    float: left;
  }
  #menu-toggle {
    font-size:    30px;
    margin-right: 10px;
  }
  .top-nav .top-nav-right {
    display: none;
  }
  #menu-toggle {
    display: block;
  }
  .hint {
    height:   42px;
    overflow: hidden;
  }
  .home nav.top-navigation {
    margin-bottom: 40px;
  }
  .search-form li.sk-form-autocomplete {
    width:         100%;
    border-radius: 5px;
    margin-bottom: 50px;
  }
  .search-form li.sk-form-datepicker {
    width:         50%;
    margin-bottom: 5px;
  }
  .search-form li.sk-form-guests {
    width:         100%;
    margin-bottom: 5px;
  }
  .search-form .sk-form-autocomplete .more-settings, .search-form .sk-form-autocomplete .more-settings a {
    top: 85px;
  }
  .search-form li.sk-form-submit {
    margin-top: 5px;
    width:      100%;
    .sk-form-item-inner {
      width:  40%;
      margin: 0 auto;
    }
  }
  section.we-offer,
  section.search-tur {
    background-size: 100% 100%;
  }
}

@media (max-width: 1000px) {

  .booking_page .akcii .akcii_img_wrap {
    max-height: 329px;
  }
  .booking_page .news .news_img_wrap {
    max-height: 372px;
  }
  .booking_page .news .info_block {
    padding: 10px 5px 16px 10px;
  }
  .similar-offers .owl-controls {
    display: none;
  }
  .booking_page .similar-offers .tur-head .price {
    position: absolute;
    z-index:  100;
    top:      52px;
  }
}

@media (max-width: 900px) {
  .blog_page .recreation-tur h1 {
    font-size: 41px;
  }
  .blog_page .recreation-tur .author {
    text-align: center;
  }
  .blog_page .comments_form textarea {
    margin-top: 10px;
  }
  .blog_page .comments_message {
    width: 76%;
  }
  .blog_page .banner {
    text-align: center;
  }
  .blog_page .akcii .percent {
    width:     100%;
    height:    45px;
    left:      0;
    font-size: 32px;
  }
  .blog_page .akcii .title {
    line-height: 22px;
  }
  .blog_page .akcii .date_time {
    font-size: 16px;
    margin:    10px 0;
  }
  .blog_page .banner img {
    margin: 0 auto;
  }
  .blog_page .nav.nav-tabs > li > a:hover,
  .blog_page .nav > li > a:focus {
    border-bottom: none !important;
  }

}

@media (min-width: 768px) and (max-width: 1195px) {
  .product_catalog .catalog .wrap_block a {
    overflow: hidden;
    height:   auto;
  }
  .product_catalog .catalog .wrap_block .border {
    padding: 54px 0 0 12px;
  }
  .product_catalog .catalog .wrap_block .border .border-head {
    margin-bottom: 5px;
  }
  .product_catalog .catalog .wrap_block .border .stars {
    font-size: 14px;
  }
  .product_catalog .catalog .wrap_block .rating_wrap {
    margin-left: 5px;
  }
  .rating_wrap .rating {
    font-size:   19px;
    margin-left: 4px;
  }
  .rating_wrap span {
    font-size: 11px;
  }
  .product_catalog .catalog .wrap_block .price {
    top: -3px;
  }
  .product_catalog .catalog .coast .btn-buy {
    margin-top: 3px;
  }
  .product_catalog .catalog .wrap_block .add_block .like {
    font-size: 14px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .home .top-navigation {
    margin-bottom: 120px;
  }
  .top-nav-right {
    text-align: center;
    .phone-number {
      color: $white_color;
    }
  }
  .search-form .sk-form-autocomplete .inner-more-settings .dropdown-menu {
    left: -314px;
  }
  .search-form .sk-form-wr-img {
    margin-right: 10px;
  }
  .search-form .sk-form-guests .sk-form-item-inner img.arrow-down {
    margin-left: 0;
  }
  .search-form .sk-form-autocomplete .inner-more-settings .dropdown-menu {
    left: -315px;
  }
}

@media only screen and (max-width: 800px) {
  #topScroll {
    right: 30px
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 767px) {
  div.subscribe .orang {
    display: none;
  }
  .home .top-navigation {
    margin-bottom: 160px;
  }
  li.phone-number {
    display: none;
  }
  button.navbar-toggled {
    line-height: 30px;
    height:      30px;
  }
  .top-nav-right {
    display: none;
  }
  /* search */
  .search-form {
    li.sk-form-item {
      width:         100%;
      margin-bottom: 5px;
      line-height:   64px;
      height:        64px;
      .sk-form-item-inner {
        border-radius: 5px;
        padding:       0;
        input {
          border-radius: 5px 0 0 5px;
        }
      }
      label {
        border-right: 0;
      }
      &.sk-form-submit {
        margin: 50px 0 0 0;
      }
    }
    .sk-form-autocomplete .more-settings {
      top: 275px;
    }
  }
  .search-form li.sk-form-submit {
    margin-top: 45px;
    width:      100%;
    .sk-form-item-inner {
      width:  100%;
      margin: 0 auto;
    }
  }
  /* end search */
  section.concerts-tur p.descr,
  section.recreation-tur p.descr,
  section.europe-tur p.descr,
  section.news p.descr,
  section.shares p.descr {
    padding: 0;
  }
  section.subscribe .orang {
    right: 0;
    top:   -200px;
    width: 200px;
    &:after {
      right: 60px;
    }
  }
  /* footer */
  .footer_menu {
    ul li {
      width:         100%;
      margin-bottom: 15px;
      a {
        border-right: 0;
      }
    }
  }
  footer .main_footer {
    .soc_wrap {
      text-align:   center;
      padding-left: 0;
    }
  }
  /* end footer */
  .search-form .sk-form-autocomplete .inner-more-settings .dropdown-menu {
    left: -15px;
  }
  .adres_info,
  .footer_icon {
    display: inline-block;
  }
  .footer .main_footer .footer_icon i {
    display: none;
  }
  .footer .main_footer .adres_info p {
    display:     inline-block;
    line-height: 20px;
  }
  .footer .footer_menu li {
    display: block;
  }
  .footer .footer_menu li a {
    line-height:  28px;
    border-right: none;
  }
  .footer .main_footer {
    text-align: center;
  }
  .product_catalog .catalog .wrap_block {
    height: 490px;
  }
  .product_catalog .catalog .wrap_block a {
    overflow: hidden;
    height:   340px;
  }
  .product_catalog .catalog .wrap_block .price {
    top: -3px;
  }
  .product_catalog .catalog .wrap_block .border .border-head {
    font-size: 21px;
  }
  .info_block,
  .add_block {
    display: inline-block
  }
  .info_block {
    margin-right: 50px;
  }
  .booking_page .akcii .info_block,
  .booking_page .news .info_block {
    margin-right: 0;
  }
  .booking_page .numbers-price .list-hotel .list-hotel-item .btn-book {
    float:      none;
    text-align: center;
  }

  .booking_page .hotel-info form {
    width:        100%;
    overflow:     hidden;
    margin-top:   20px;
    padding-left: 16px;
  }
  .slider-nav {
    justify-content: flex-start;
  }
  .block-free-date .ui-datepicker-multi-2 .ui-datepicker-group {
    width:  90%;
    margin: 0 auto;
    float:  none;
  }
  .booking_page .akcii,
  .booking_page .news {
    margin-right: -5px;
    margin-left:  -5px;
  }
}

@media (max-width: 640px) {
  .booking_page .about-hotel .slider-nav div {
    max-width: 76px;
    height:    76px;
  }
}

@media (max-width: 600px) {
  .booking_page .about-hotel .slider-nav div {
