	bottom: -15px;
	width: 20px;
	height: 10px;
	border-style: solid;
	border-width: 0 20px 20px 20px;
	border-color: transparent transparent #ffffff transparent;
	right: -webkit-calc(50% - 20px);
	right: calc(50% - 20px);
}

.catalog .wrap_block .new {
