/* -----range slider style-----*/
.range-slider {
	margin-top: 20px;
	margin-bottom: 20px;
}

input[type=range] {
	-webkit-appearance: none;
	width: 100%;
}

input[type=range]::-webkit-slider-thumb {
	-webkit-appearance: none;
}

input[type=range]:focus {
	outline: none;
}

input[type=range]::-ms-track {
	width: 100%;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	color: transparent;
}

input[type=range]::-webkit-slider-thumb {
	-webkit-appearance: none;
	border: 5px solid #07af61;
	height: 16px;
	width: 16px;
	-webkit-border-radius: 50%;
	border-radius: 50%;
	background: #ffffff;
	cursor: pointer;
	margin-top: -7px;
	/*box-shadow: 1px 1px 1px #000000,
	0px 0 1px #0d0d0d;*/
}

input[type=range]::-moz-range-thumb {
	-webkit-appearance: none;
	border: 5px solid #07af61;
	height: 16px;
	width: 16px;
	border-radius: 50%;
	background: #ffffff;
	cursor: pointer;
	margin-top: -7px;
	/*box-shadow: 1px 1px 1px #000000,
	0px 0 1px #0d0d0d;*/
}

input[type=range]::-ms-thumb {
	-webkit-appearance: none;
	border: 5px solid #07af61;
	height: 16px;
	width: 16px;
	border-radius: 50%;
	background: #ffffff;
	cursor: pointer;
	margin-top: -7px;
	/*box-shadow: 1px 1px 1px #000000,
	0px 0 1px #0d0d0d;*/
}

input[type=range]::-webkit-slider-runnable-track {
	-webkit-appearance: none;
	border: 0.2px solid #07af61;
	height: 4px;
	width: 100%;
	-webkit-border-radius: 0;
	border-radius: 0;
	background: #07af61;
	cursor: pointer;
	margin-top: -7px;
	/*box-shadow: 1px 1px 1px #000000,
	0px 0 1px #0d0d0d;*/
}

input[type=range]::-webkit-slider-runnable-track:focus {
	background: #07af61;
}

input[type=range]::-moz-range-track {
	-webkit-appearance: none;
	border: 0.2px solid #bcb8b8;
	height: 4px;
	width: 100%;
	border-radius: 0;
	background: #07af61;
	cursor: pointer;
	margin-top: -7px;
	/*box-shadow: 1px 1px 1px #000000,
	0px 0 1px #0d0d0d;*/
}

input[type=range]::-ms-track {
	width: 100%;
	height: 4px;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	border-width: 4px 0;
	color: transparent;
}

input[type=range]::-ms-fill-lower {
	background: #07af61;
	border: 0.2px solid #07af61;
	border-radius: 2.6px;
	box-shadow: 1px 1px 1px #bcb8b8, 0 1px #bcb8b8;
}

input[type=range]:focus::-ms-fill-lower {
	background: #07af61;
}

input[type=range]::-ms-fill-upper {
	background: #07af61;
	border: 0.2px solid #07af61;
	border-radius: 2.6px;
	box-shadow: 1px 1px 1px #bcb8b8, 0 1px #7bc8a4;
}

input[type=range]:focus::-ms-fill-upper {
	background: #07af61;
}

/* -----range slider style end-----*/
