section {
	font-family: 'ProximaNova', sans-serif;
	padding-top: 30px;
	padding-bottom: 30px;
	.hr {
		position: relative;
		.inner {
			color: $green;
			margin: 0 auto;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 20px;
				border-top: 1px solid $green;
				width: 45%;
				height: 0;
			}
			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: 20px;
				border-top: 1px solid $green;
				width: 45%;
				height: 0;
			}
		}

	}
	p.descr {
		text-align: center;
		padding: 0 70px;
		color: #393b3d;
		font-size: 22px;
		line-height: 38px;
		margin-top: 30px;
		margin-bottom: 30px;
	}
	.wr-button {
		button {
			border: 1px solid $green;
			border-radius: 30px;
			color: $green;
			background: transparent;
			outline: none;
			font-size: 26px;
			line-height: 36px;
			margin-top: 30px;
			margin-bottom: 30px;
			padding: 10px 40px;
			&:hover,
			&:active {
				color: $green;
				background: transparent;
				outline: none;
			}
		}
	}
	& div a,
	& div a:hover,
	& div a:active {
		text-decoration: none;
	}
	& > .container {
		& > h2 {
			margin: 0;
			font-size: 37px;
			padding-bottom: 30px;
			color: #393b3d;
		}
		& > h2,
		& h3 {
			margin-top: 0;
			margin-bottom: 0;
			text-align: center;
		}
	}
}

section.popular-way {
	--bg_popular_way: #F1F1F1;
	background: var(--bg_popular_way);
	padding-bottom: 86px;
	.hr.bus .inner {
		background: url('../images/icon-(13).png') no-repeat;
		width: 42px;
		--height_recreation: 39px;
		height: var(--height_recreation);
		&:before,
		&:after {
			top: calc(var(--height_recreation) / 2);
		}
	}
	.wr-popular {
		margin-top: 18px;
		&:hover {
			-webkit-transition: margin-top 0.2s ease-out 0.1s;
			-moz-transition: margin-top 0.2s ease-out 0.1s;
			-o-transition: margin-top 0.2s ease-out 0.1s;
			transition: margin-top 0.2s ease-out 0.1s;
			box-shadow: -3px 3px 7px 1px #ccc;
			margin-top: 14px;
		}
		.popular-content img {
			width: 100%;
			height: auto;
		}
		h3 {
			margin-top: 0;
			margin-bottom: 0;
			padding-top: 12px;
			padding-bottom: 6px;
			background: #FDFDFD;
			color: #3a3b3d;
			font-size: 26px;
			line-height: 38px;
		}
		ul {
			color: $green;
			padding: 26px 24px 12px 24px;
			background: #FDFDFD;
			margin-bottom: 0;
			li {
				cursor: pointer;
				border-bottom: 1px solid #E8E8E8;
				color: #07af61;
				font-size: 22px;
				line-height: 40px;
				&:last-child {
					border-bottom: 0;
				}
				img {
					margin-top: 13px;
				}
			}
		}
		img {
			float: right;
		}
	}
}

section.advantages {
	--bg_advantages: #FDFDFD;
	background: var(--bg_advantages);
	text-align: center;
	.hr.medal .inner {
		background: url('../images/award-medal-of-number-one-with-olive-branches.png');
		width: 47px;
		--height_recreation: 38px;
		height: var(--height_recreation);
		&:before,
		&:after {
			top: calc(var(--height_recreation) / 2);
		}
	}
	.advantages-content > img {
		padding-top: 50px;
		padding-bottom: 40px;
	}
	h3.advantages-title {
		margin-top: 0;
		margin-bottom: 0;
		font-family: 'ProximaNova', sans-serif;
		color: #3a3b3d;
		font-size: 26px;
		line-height: 34px;
		padding: 0 40px;
	}
}

section.we-offer {
	background: url('../images/we-offer-bg.png') no-repeat top center;
	color: #fff;
	text-align: center;
	.link-top-item {
		display: inline-block;
	}
	.hr.firstPlace .inner {
		background: url('../images/icon-(14).png') no-repeat;
		width: 39px;
		--height_recreation: 49px;
		height: var(--height_recreation);
		&:before,
		&:after {
			top: calc(var(--height_recreation) / 2);
			border-color: #fff;
		}
	}
	.we-offer-content {
		margin-top: 33px;
	}
	a, h2, h3 {
		color: #fff;
	}
	.we_offer-items img {
		&:hover {
			-webkit-transition: margin-top 0.2s ease-out 0.1s;
			-moz-transition: margin-top 0.2s ease-out 0.1s;
			-o-transition: margin-top 0.2s ease-out 0.1s;
			transition: margin-top 0.2s ease-out 0.1s;
			margin-top: -3px;
			& + h3 {
				margin-bottom: 0;
				-webkit-transition: margin-top 0.2s ease-out 0.1s;
				-moz-transition: margin-top 0.2s ease-out 0.1s;
				-o-transition: margin-top 0.2s ease-out 0.1s;
				transition: margin-top 0.2s ease-out 0.1s;
				margin-top: 23px;
			}
		}
	}
	.we_offer-items h3 {
		margin-bottom: 0;
		margin-top: 20px;
		font-size: 22px;
		line-height: 30px;
		padding: 0 30px;
	}
}

