section.search-tur {
	background: url("../images/search-bottom-bg.png") no-repeat top center;
	min-height: 385px;
	height: auto;
	h1 {
		color: #fff;
		font-size: 42px;
		margin-bottom: 40px;
		text-align: center;
	}
}

div.subscribe {
	padding-top: 0;
	background: url(../images/subscribe-bg.png) no-repeat top center;
	min-height: 629px;
	.logo {
		padding-top: 95px;
	}
	.descr {
		background: rgba(255, 255, 255, 0.3);
		color: #fff;
		text-transform: uppercase;
		padding: 30px;
		font-size: 22px;
		line-height: 30px;
		text-align: center;
	}
	form {
		position: relative;
		text-align: center;
		max-height: 186px;
		max-width: 443px;
		background: rgba(0, 0, 0, 0.4);
		margin: 200px auto 0;
		padding: 30px;
		label {
			height: 54px;
			width: 100%;
			&[for=subscribeEmail] {
				margin-bottom: 20px;
			}
			input {
			}
		}
	}
	input[type=email] {
		background: transparent;
		border: 1px solid #fff;
		display: block;
		width: 100%;
		line-height: 50px;
		margin-bottom: 20px;
		padding-left: 20px;
		padding-right: 20px;
		color: #fff;
		font-size: 21px;
		&::-webkit-input-placeholder {
			text-align: center;
			color: #fff;
			font-size: 21px;
			line-height: 17px;
		}

		&:-moz-placeholder { /* Firefox 18- */
			text-align: center;
			color: #fff;
			font-size: 21px;
			line-height: 17px;
		}

		&::-moz-placeholder { /* Firefox 19+ */
			text-align: center;
			color: #fff;
			font-size: 21px;
			line-height: 17px;
		}

		&:-ms-input-placeholder {
			text-align: center;
			color: #fff;
			font-size: 21px;
			line-height: 17px;
		}
	}
	input[type=submit] {
		border: 0;
		background: $green;
		color: #fff;
		width: 100%;
		height: 100%;
		border-radius: 0;
		font-family: $ProximaNova;
		font-size: 22px;
		font-weight: 300;
	}
	.orang {
		position: absolute;
		background: #FD8D00;
		width: 264px;
		height: 150px;
		padding-top: 33px;
		color: #fefefe;
		font-size: 30px;
		text-align: center;
		display: inline-block;
		top: -200px;
		right: -200px;
		&:after {
			position: absolute;
			bottom: -50px;
			right: 120px;
			content: '';
			height: 0;
			width: 0;
			border-top: 0 solid transparent;
			border-bottom: 60px solid transparent;
			border-left: 60px solid #FD8D00;
		}
	}
}

#nextTurSlider,
#prevTurSlider,
#nextRecreationSlider,
#prevRecreationSlider,
#nextEuropeSlider,
#prevEuropeSlider,
#nextNewsSlider,
#prevNewsSlider,
#nextSharesSlider,
#prevSharesSlider {
	position: absolute;
	top: 45%;
	cursor: pointer;
}

#nextTurSlider,
#nextRecreationSlider,
#nextEuropeSlider,
#nextNewsSlider,
#nextSharesSlider {
	right: -60px;
}

#prevTurSlider,
#prevRecreationSlider,
#prevEuropeSlider,
#prevSharesSlider,
#prevNewsSlider {
	left: -60px;
}
