.blog {
	time {
		color: #6b6a6a;
		font-weight: 300;
		font-size: 12px;
	}
	.last-publication {
		.publication-wrap {
			.row {
				display: -webkit-box;
				display: -webkit-flex;
				display: -ms-flexbox;
				display: flex;
				flex-wrap: wrap;
				& > [class*='col-'] {
					display: flex;
					flex-direction: column;
				}
			}
			.info_wrap {
				h3 {
					text-align: left;
					color: #3a3b3d;
					font-weight: 700;
					font-size: 18px;
					font-family: $ProximaNova;
					margin-top: 20px;
					margin-bottom: 15px;
				}
			}
		}
		.post-item {
			border: 1px solid #E5E5E7;
			height: 100%;
			margin-bottom: 30px;
			.info_wrap {
				font-family: $ProximaNova;
				padding: 14px 12px;
				.tags-info {
					color: #07af61;
					font-weight: 300;
					font-size: 12px;
					text-transform: uppercase;
				}
				h3 a {
					color: #3a3b3d;
					font-weight: 700;
					font-size: 19px;
				}
				p {
					color: #3a3b3d;
					font-weight: 300;
					font-size: 16px;
				}
			}
		}
	}
	.wrap-news-block-tabs {
		.nav-tabs {
			li.active > a {
				background: #FD9008;
			}
		}
	}
	.tabs {
		.tab-content .tab-pane {
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			flex-wrap: wrap;
			& > [class*='col-'] {
				display: flex;
				flex-direction: column;
			}
		}
	}
	.nav_menu li {
		display: inline-block;
		padding: 0;
		line-height: 56px;
		border-bottom: 7px solid transparent;
		border-top: 7px solid transparent;
		&:hover {
			border-bottom-color: #FD9008;
		}
		&:first-child a {
			padding-left: 0;
		}
		&:last-child a {
			border: none;
		}
		a {
			text-decoration: none;
			font-family: $ProximaNova;
			font-size: 18px;
			font-weight: 400;
			color: #fff;
			padding-right: 20px;
			padding-left: 20px;
			border-right: 1px solid #fff;
		}
	}
	.preview-item .img_wrap img {
		padding-right: 7px;
	}
	.soc_wrap_head {
		margin-bottom: 0;
		li {
			margin-top: 7px;
			margin-bottom: 9px;
			vertical-align: top;
			height: 54px;
			line-height: 54px;
		}
		.wr-search {
			width: 135px;
			.box {
				margin-top: 9px;
			}
			.container-2 {
				vertical-align: middle;
				white-space: nowrap;
				position: relative;
				float: right;
				.icon {
					position: absolute;
					z-index: 1;
					color: #fd9008;
					font-size: 26px;
					margin-top: -9px;
					margin-left: 5px;
				}
				input#search {
					width: 35px;
					height: 35px;
					background: transparent;
					border: none;
					font-size: 10pt;
					float: left;
					padding-left: 35px;
					color: #fff;

					-webkit-transition: width .55s ease;
					-moz-transition: width .55s ease;
					-ms-transition: width .55s ease;
					-o-transition: width .55s ease;
					transition: width .55s ease;
					&::-webkit-input-placeholder {
						color: #65737e;
					}
					&:-moz-placeholder { /* Firefox 18- */
						color: #65737e;
					}
					&::-moz-placeholder { /* Firefox 19+ */
						color: #65737e;
					}
					&:-ms-input-placeholder {
						color: #65737e;
					}
				}
				input#search:focus,
				input#search:active {
					outline: none;
					width: 170px;
					border: 1px solid #fd9008;
					border-radius: 3px;
				}

				&:hover input#search {
					width: 170px;
					border: 1px solid #fd9008;
					border-radius: 3px;
				}
			}
		}
	}
}

.blog_page {
	.comments_img {
		display: inline-block;
		margin-right: 10px;
		width: 60px;
		height: 60px;
		background-color: white;
		border-radius: 50%;
		text-align: center;
		overflow: hidden;
		vertical-align: top;
	}
}