footer {
  .footer_menu {
    background-color: #575758;
    padding:          20px 0;
    text-align:       center;
    ul {
      margin-bottom: 0;
    }
    li {
      list-style-type: none;
      display:         inline-block;
      a {
        text-decoration: none;
        font-family: $ProximaNova;
        font-size:       18px;
        font-weight:     300;
        line-height:     17px;
        color:           #fff;
        text-transform:  uppercase;
        padding:         0 30px;
        border-right:    1px solid #fff;
      }
      &:last-of-type a {
        border-right: 0;
      }
    }
  }

  .main_footer {
    padding: 25px 0;
    .footer_icon i {
      font-size:    41px;
      margin-right: 15px;
    }
    .soc_wrap {
      padding: 0 0 0 44px;
      li {
        list-style-type: none;
        display:         inline-block;
        width:           41px;
        height:          41px;
        margin-right:    11px;
        a {
          display:               inline-block;
          width:                 100%;
          height:                100%;
          -webkit-border-radius: 50%;
          border-radius:         50%;
          color:                 #fff;
          text-align:            center;
          i {
            font-size:   19px;
            line-height: 41px;
          }
          &.fb {
            background-color: #3660b8;
          }
          &.vk {
            background-color: #3d6ad0;
          }
          &.yt {
            background-color: #e83838;
          }
          &.google {
            background-color: #f3513d;
          }
          &.twitter {
            background-color: #1ac0ff;
          }
        }
      }
    }
    .adres_info {
      p {
        margin-bottom: 5px;
        color:         #1a181d;
        font-family: $ProximaNova;
        font-size:     14px;
        font-weight:   300;
        text-align:    left;
        line-height:   18px;
      }

    }
  }
}

.no-padding {
  padding: 0 !important;
}

.no-padding-left {
  padding-left: 0 !important;;
}

.no-padding-right {
  padding-right: 0 !important;;
}