@import url(https://fonts.googleapis.com/css?family=Roboto:400,900italic,300);
@import url(https://fonts.googleapis.com/css?family=Merriweather:400,700,900,300,300italic,400italic,700italic,900italic);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,700,800&subset=latin,cyrillic);

@font-face {
  font-family: 'ProximaNova';
  src:         url('../fonts/proxima/ProximaNova.otf') format('truetype');
  src:         url('../fonts/proxima/ProximaNova.woff') format('woff');
  src:         url('../fonts/proxima/ProximaNova.woff2') format('woff2');
}

@font-face {
  font-family: 'Intro Regular';
  src:         url("../fonts/Intro-Regular.woff") format("opentype");
}

@font-face {
  font-family: 'Intro';
  src:         url("../fonts/Intro-Bold.woff") format("opentype");
}
