.modal-backdrop.in {
	z-index: 0 !important;
}

.book_modal .modal-title {
	color: #07af61;
	font-size: 34px;
	font-weight: 400;
	margin-top: 30px;
	margin-bottom: 63px;
}

.book_modal .modal-content {
	background-color: transparent !important;
	overflow: hidden;
	/*box-shadow: none;
	border: 0;*/
	font-family: "ProximaNova";
}

.book_modal .modal-body,
.book_modal .modal-footer {
	background: #fff !important;
}

.book_modal p {
	color: white;
	font-size: 24px;
	font-weight: 400;
	line-height: 38px;
}

.book_modal .bonus {
	color: white;
	font-size: 60px;
	font-weight: 400;
	line-height: 38px;
	margin-top: 30px;
}

.book_modal .coin {
	position: relative;
	padding: 30px;
	width: 100%;
	height: 51px;
	background: url("../images/coin3.png") no-repeat center;
}

.book_modal .coin:before {
	position: absolute;
	left: 0;
	top: 62%;
	content: "";
	width: 45%;
	height: 2px;
	background-color: #108e53;
}

.book_modal .coin:after {
	position: absolute;
	right: 0;
	top: 62%;
	content: "";
	width: 45%;
	height: 2px;
	background-color: #108e53;
}

.book_modal .modal-open .modal {
	background: rgba(0, 0, 0, 0.7);
}

.book_modal .modal-header {
	padding: 15px 0;
	background: rgba(0, 0, 0, 0.5);
}

.book_modal .modal-header .close {
	padding: 0 15px 0 0;
	color: #fff;
	opacity: 1;
	margin-top: -2px;
	outline: none;
}

.book_modal .quick_book_btn {
	font-family: $ProximaNova;
	margin-top: 23px;
	width: 262px;
	height: 45px;
	background-color: #ADA9A5;
	border-radius: 3px;
	color: white;
	font-size: 20px;
	font-weight: 400;
	line-height: 20px;
	text-shadow: none;
	box-shadow: none;
	outline: none;
	border: none;
	background-image: none;
}

.book_modal .book_title_wrap {
	margin-top: -1px;
	width: 100%;
	background-color: #07af61;
	height: 69px;
}

.book_modal .book_title_wrap h3 {
	margin: 0;
	color: white;
	font-size: 20px;
	font-weight: 400;
	line-height: 71px;
	font-family: "ProximaNova";
}

.book_modal .modal-body {
	padding: 0;
}

.book_modal .modal-body form.form-horizontal {
	padding: 15px;
}

.book_modal .modal-body label {
	text-align: left !important;
	font-weight: 400;
	position: relative;
	&:before {
		content: '*';
		position: absolute;
		left: 5px;
		z-index: 99;
	}
}

.book_modal .modal-body .main_form_wrap.first {
	margin: 0 -15px 0 0;
	border-right: 1px solid #eee;
}

.book_modal .modal-body .main_form_wrap.last {
	margin: 0 0 0 -15px;
}

.book_modal .modal-body .book_register_btn {
	margin-top: 23px;
	height: 46px;
	background-color: #f78c04;
	border-radius: 3px;
	color: white;
	font-family: $ProximaNova;
	font-size: 20px;
	font-weight: 400;
	line-height: 20px;
	box-shadow: none;
	text-shadow: none;
	outline: none;
	border: none;
	background-image: none;
}
