#ui-datepicker-div {
	margin-top: 5px;
}

.booking_page {
	.akcii,
	.news {
		h3 {
			text-align: left;
		}
	}
	.news {
		margin-top: 30px;
	}
	.akcii {
		margin-top: 42px;
	}
	.search-form {
		margin-top: 20px;
		li {
			height: 58px;
			.sk-form-item-inner {
				padding-top: 5px;
				padding-bottom: 5px;
				label {
					height: 44px;
					line-height: 44px;
				}
			}
			&.sk-form-submit {
				.sk-form-item-inner {
					padding: 0;
				}
				a.btn.btn-default {
					height: 54px;
					padding: 0;
				}
			}
		}
		.guests .guests-menu {
			margin-top: -21px;
		}
	}
	.back-to-result {
		margin-top: 32px;
		margin-bottom: 27px;
		color: #07af61;
		font-size: 20px;
		font-family: $ProximaNova;
		padding-top: 5px;
		padding-bottom: 5px;
	}
	.reserve {
		background: #FDFDFD;
		padding: 27px 18px;
		margin-bottom: 14px;
		.btn-default.active.focus,
		.btn-default.active:focus,
		.btn-default.active.active,
		.btn-default.active:active,
		.btn-default.active:hover,
		.btn-default:active.focus,
		.btn-default:active:focus,
		.btn-default:active:hover,
		.btn-default,
		.open > .dropdown-toggle.btn-default.focus,
		.open > .dropdown-toggle.btn-default:focus,
		.open > .dropdown-toggle.btn-default:hover {
			color: #929292;
			background: transparent;
			box-shadow: none;
		}
		.truestar {
			font-size: 16px;
			color: #FDAD00;
			cursor: pointer;
		}
		h3 {
			margin-top: 0;
			color: #3a3b3d;
			font-size: 32px;
			font-family: $ProximaNova;
		}
		address {
			color: #79837E;
			i {
				font-size: 26px;
				float: left;
				& + p {
					color: #636365;
					font-size: 14px;
					font-family: $ProximaNova;
					text-decoration: none;
				}
			}
		}
		form {
			position: relative;
			&:after {
				position: absolute;
				top: -2px;
				right: -20px;
				width: 10px;
				height: 102px;
				content: '';
				background: url("../../../../public/src/images/reserve_sbr.png") no-repeat;
			}
		}
		form p {
			margin-bottom: 10px;
			.dropdown-menu {
				width: 100%;
				min-width: 100%;
				top: 35px;
				border-top: 0;
				border-bottom: 1px solid #929292;
				border-left: 1px solid #929292;
				border-right: 1px solid #929292;
				border-radius: 0;
				padding: 10px;
				box-shadow: none;
				& li:first-child {
					border-top: 4px solid #E0E2E3;
				}
				& li a {
					color: #929292;
				}
				& li.active a {
					background: transparent;
					&:hover {
						color: #929292;
					}
				}
			}
			& > label {
				margin-bottom: 0;
				button {
					white-space: initial;
				}
			}
			& .btn-group,
			& .btn-group > button,
			& > label,
			& > label > span {
				width: 100%;
				display: inline-block;
			}
			.btn-group > button {
				border: solid 1px #929292;
				border-radius: 0;
				height: 44px;
				text-align: left;
				color: #929292;
				&,
				&:hover,
				&:active {
					background: none;
				}
				.caret {
					position: absolute;
					right: 10px;
					top: 13px;
					background: url("../../../../public/src/images/arrow-down.png") no-repeat center center;
					width: 15px;
					height: 9px;
					border: 0;
					margin-top: 5px;
				}
			}
		}
		.btn-group.open button {
			background: #fff !important;
			box-shadow: none;
			color: #929292;
		}
	}

	.reserve .dropdown-menu {
		a {
			padding: 0;
			label {
				white-space: pre-wrap;
			}
		}
		li:hover a {
			background: transparent;
			outline: none;
		}
		li.active a {
			color: #929292;
		}
		input[type=checkbox] {
			display: none;
		}
		input[type=checkbox]:checked + span.checkboxx {
			display: inline-block;
			width: 18px;
			height: 18px;
			border: 1px solid #c3c5c4;
			background: url(../../../../public/src/images/check35.png) center center no-repeat;
		}
		input[type=checkbox] + span.checkboxx {
			vertical-align: middle;
			display: inline-block;
			width: 18px;
			height: 18px;
			border: 1px solid #c3c5c4;
			margin-right: 10px;
		}
	}

	.reserve .dropdown-menu a label {
		margin: 0;
		padding: 10px 0;
	}

	.wr-button-reserve {
		font-family: 'proxima-nova', sans-serif;
		p {
			text-align: center;
			color: #ff9108;
			font-weight: 400;
			font-size: 30px;
			margin: 0;
			span {
				color: #676767;
				font-weight: 400;
				font-size: 16px;
			}
		}
		a.btn.btn-default {
			background: #07AE60;
			height: 58px;
			width: 250px;
			border-radius: 3px;
			line-height: 58px;
			padding: 0;
			color: #ffffff;
			font-weight: 700;
			font-size: 26px;
		}
		& + .like {
			color: #616563;
			font-weight: 400;
			font-size: 18px;
			line-height: 36px;
			span {
				color: #ff9108;
				font-size: 24px;
				line-height: 24px;
			}
			i {
				font-size: 22px;
				color: #ADADAD;
			}
		}
	}
	#accordion {
		.ui-icon-triangle-1-s,
		.ui-icon-triangle-1-e {
			display: none;
		}
		h3 {
			border: 0;
			background: #E0E2E3;
			outline: none;
			color: #3a3b3d;
			font-family: $ProximaNova;
			font-weight: 300;
			font-size: 18px;
			line-height: 36px;
			text-align: left;
			border-right: 4px solid #E0E2E3;
			padding: 0 0 0 12px;
			border-radius: 3px;
		}
		h3.ui-state-active {
			border-right: 4px solid #07AE60;
		}
		div.ui-accordion-content {
			padding: 0;
			border: 0;
			ul {
				li {
					font-family: $ProximaNova;
					cursor: pointer;
					color: #3a3b3d;
					border-bottom: 1px solid #E8E8E8;
					line-height: 36px;
					font-size: 12px;
					padding-left: 12px;
					&.active {
						color: #07AE60;
					}
				}
			}
		}
	}
	.image-area {
		margin: 0 auto;
	}

	.main-image {
		position: relative;
		text-align: center;
		overflow: hidden;
		margin-bottom: 15px;
		img {
			width: 100%;
			height: auto;
		}
	}

	.description-hotel {
		margin-top: 36px;
		color: #3a3b3d;
		font-size: 16px;
		line-height: 36px;
		font-family: 'Merriweather', serif;
	}

	.main-image div {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: auto;
		-moz-opacity: 0;
		opacity: 0;
		z-index: -1;
	}

	.sub-image {
		& > div {
			width: 12.5%;
			margin: 0;
			float: left;
			cursor: pointer;
			text-align: center;
			padding-right: 5px;
			&:nth-child(8n) {
				padding-right: 0;
			}
		}
		img {
			width: 100%;
			margin: 0 0 3px 0;
		}
	}

	.numbers-and-price {
		padding-bottom: 13px;
		border-bottom: 1px dashed $green;
		margin-bottom: 9px;
		color: #393b3d;
		font-weight: 300;
		font-size: 26px;
		line-height: 17px;
		font-family: $ProximaNova;
	}

	.hotel-info {
		padding-top: 25px;
		padding-bottom: 25px;
		.hotel-item-img {
			width: 30%;

			padding-right: 10px;
		}
		h2,
		.hotel-item-info {
			float: left;
		}
		h2 {
			display: inline-block;
			width: 70%;
			color: #3a3b3d;
			font-size: 20px;
			font-family: $ProximaNova;
			margin: 0;
			strong {
				border-left: 1px solid #ccc;
				padding-left: 5px;
				padding-right: 5px;
			}
		}
		.hotel-item-info {
			margin-top: 18px;
			color: #58595b;
			font-size: 11px;
			line-height: 20px;
			width: 70%;
			display: inline-block;
			font-family: 'Merriweather', serif;
		}
	}

	.multy-select {
		overflow: visible;
	}

	.hotel-info-block {
		background: #FDFDFD;
		padding: 17px;
		margin-bottom: 84px;
	}
	.breadcrumb {
		background: transparent;
	}
	.ui-accordion-content {
		background: #fdfdfd;
	}
	.more-settings {
		display: none;
	}
	.back-result {
		line-height: 76px;
		a {
			color: #07af61;
			font-size: 20px;
			font-family: $ProximaNova;
			text-decoration: none;
		}
	}
	.breadcrumb a {
		color: #07af61;
	}
	#wrapAccordion {
		position: relative;
	}
	.ui-accordion {
		position: static;
	}
	.fixedSidebarScroll {
		position: fixed;
		top: 0;
		width: 100%;
	}
	.fixedSidebar {
		position: relative;
	}
	.ui-accordion-content {
		p {
			font-family: $ProximaNova;
			cursor: pointer;
			color: #3a3b3d;
			border-bottom: 1px solid #e8e8e8;
			line-height: 36px;
			font-size: 12px;
			padding-left: 12px;
		}
	}
	.datepicker-hotel {
		margin-top: 7px;
		font-family: $ProximaNova;
		.ui-datepicker-inline {
			width: 95% !important;
			margin: 0 auto;
			padding: 0;
			border: 0;
			.ui-datepicker-header {
				border: 0;
				border-radius: 0;
				background: $green;
				height: 32px;
				color: #fff;
			}
			.ui-datepicker-group-first {
				padding-right: 5px;
			}
			.ui-datepicker-group-last {
				padding-left: 5px;
			}
			.ui-datepicker-calendar {
				margin: 0;
				width: 100%;
			}
			td, th {
				margin: 0;
				border: 1px solid #DEDEDE;
				width: 40px;
				padding: 4px;
				a, span {
					margin: 0;
					padding: 0;
				}
			}
			td, th {
				a, span {
					text-align: center;
					height: 40px;
					line-height: 40px;
					background: none;
					border: 0;
				}
			}
		}
	}
	.numbers-price {
		padding-top: 15px;
	}
	#fixedSidebar {
		& > li.sidebar-header {
			&.active a {
				border-right: 4px solid #07af61;
				border-radius: 4px;
			}
			& > a {
				display: block;
				border-radius: 4px;
				margin-bottom: 2px;
				font-family: $ProximaNova;
				background: #E0E2E3;
				color: #3a3b3d;
				font-size: 14px;
				font-weight: 300;
				line-height: 36px;
				text-align: left;
				padding-left: 13px;
				text-decoration: none;
			}
			.sidebar-inner {
				&.active a {
					color: #07af61;
				}
				&:last-child a {
					border-bottom: 0;
				}
				& > a {
					display: block;
					font-family: $ProximaNova;
					color: #68696a;
					font-size: 14px;
					font-weight: 300;
					line-height: 36px;
					text-align: left;
					padding-left: 13px;
					border-bottom: 1px solid #E8E8E8;
				}
			}
		}
	}
	.numbers-price .list-hotel .list-hotel-item {
		.final-book {
			border-bottom: 0;
		}
	}
	.list-rooms {
		.room-item-wr-img {
			width: 96px;
			height: 96px;
			margin-right: 18px;
			float: left;
			img {
				width: 100%;
			}
		}
		.room-item-header {
			display: inline-block;
			vertical-align: top;
			font-family: $ProximaNova;
			color: #3a3b3d;
			font-size: 18px;
			font-weight: 400;
			line-height: 30px;
			text-align: left;
			margin: 0;
		}
		.room-item-info {
			font-family: 'Merriweather', serif;
			color: #58595b;
			font-size: 9.5px;
			font-weight: 400;
			line-height: 20px;
			text-align: left;
		}
		.list-services {
			li {
				color: #AAAAAA;
				display: inline-block;
				position: relative;
				float: left;
				width: 100%;
				input[type=radio] {
					position: absolute;
					visibility: hidden;
				}
				label {
					display: inline-block;
					position: relative;
					padding: 0 0 0 15px;
					margin: 0;
					z-index: 9;
					cursor: pointer;
					-webkit-transition: all 0.25s linear;
					font-family: $ProximaNova;
					color: #68696a;
					text-align: left;
					width: 70%;
					span {
						font-size: 13.87px;
						font-weight: 300;
						text-align: left;
						margin-right: 10px;
						line-height: 20px;
					}
					small {
						color: #959595;
						font-size: 10.4px;
						font-weight: 300;
						text-align: left;
						float: right;
						line-height: 20px;
					}
				}
				.check {
					display: block;
					position: absolute;
					border: 1px solid #AAAAAA;
					border-radius: 100%;
					height: 10px;
					width: 10px;
					top: 4px;
					left: 0;
					z-index: 5;
					transition: border .25s linear;
					-webkit-transition: border .25s linear;
					&::before {
						display: block;
						position: absolute;
						content: '';
						border-radius: 100%;
						height: 4px;
						width: 4px;
						top: 2px;
						left: 2px;
						margin: auto;
						transition: background 0.25s linear;
						-webkit-transition: background 0.25s linear;
					}
				}
				input[type=radio]:checked ~ .check::before {
					background: #07AB5E;
				}
			}
		}
		.group-guests {
			.form-group {
				label {
					font-family: $ProximaNova;
					color: #68696a;
					font-size: 12px;
					font-weight: 300;
					line-height: 22px;
					text-align: left;
					select {
						margin-left: 12px;
						padding: 0 0 0 6px;
					}
				}
			}
		}
		.add_purpose {
			p {
				label {
					font-family: $ProximaNova;
					color: #07af61;
					font-size: 12px;
					font-weight: 300;
					line-height: 36px;
					text-align: left;
				}
			}
			.hide-native-select {
				button.multiselect {
					border-radius: 0;
					background: transparent;
					box-shadow: none;
					width: 220px;
					border-color: #C3C5C4;
					overflow: hidden;
				}
				.dropdown-menu {
					width: 100%;
					min-width: 100px;
					margin: -2px 0 0 0;
					border-top: 0;
					border-left: 1px solid #C3C5C4;
					border-right: 1px solid #C3C5C4;
					border-bottom: 1px solid #C3C5C4;
					border-radius: 0;
					padding: 11px;
					& > .active a {
						background: none;
					}
					li {
						&:last-child a {
							border-bottom: 0;
						}
						a {
							border-bottom: 1px solid #E8E8E8;
							outline: none;
							padding-left: 0;
							&:hover,
							&:active,
							&:focus {
								background: none;
							}
							label {
								line-height: 16px;
								font-family: $ProximaNova;
								color: #68696a;
								font-size: 12px;
								font-weight: 300;
								text-align: left;
								input[type=checkbox] {
									display: none;
									&:checked + span.checkboxx {
										display: inline-block;
										width: 18px;
										height: 18px;
										border: 1px solid #c3c5c4;
										background: url(../../../../public/src/images/check35.png) center center no-repeat;
									}
									& + span.checkboxx {
										vertical-align: middle;
										display: inline-block;
										width: 18px;
										height: 18px;
										border: 1px solid #c3c5c4;
										margin-right: 10px;
									}
								}
							}
						}
					}
				}
			}
		}
		.room-info > form {
			overflow: visible;
		}
		.final-book {
			padding-bottom: 40px;
			border-bottom: 1px dashed #c2c4c4;
			margin-bottom: 26px;
			.form-group {
				label {
					font-family: $ProximaNova;
					color: #68696a;
					font-size: 12px;
					font-weight: 300;
					line-height: 12px;
					text-align: left;
					select {
						margin-top: -5px;
						float: right;
						margin-left: 12px;
						padding: 0 0 0 6px;
						width: 46px;
						height: 23px;
						border-radius: 0;
						-webkit-appearance: none;
						background: url(../images/arrow-down.png) no-repeat 80%;
						line-height: 1em;
						-moz-appearance: none;
						text-indent: .01px;
						text-overflow: '';
						-ms-appearance: none;
						appearance: none !important;
					}
				}
			}
			.total-price {
				.money {
					margin-bottom: 0;
					font-family: $ProximaNova;
					color: #3a3b3d;
					font-size: 13px;
					font-weight: 400;
					line-height: 30px;
					text-align: left;
					span {
						font-family: $ProximaNova;
						color: #ff9108;
						font-size: 18px;
						font-weight: 700;
						line-height: 25px;
						text-align: left;
					}
				}
				.for-who,
				.from-to {
					font-family: $ProximaNova;
					color: #3a3b3d;
					font-size: 12px;
					font-weight: 400;
					line-height: 20px;
					text-align: left;
					padding-left: 60px;
					margin-bottom: 0;
				}
			}
			.btn-book {
				text-align: right;
				button {
					width: 160px;
					height: 50px;
					font-family: $ProximaNova;
					color: #ffffff;
					font-size: 18px;
					font-weight: 700;
					line-height: 17px;
					text-align: center;
					background: #F08600;
					border: 0;
					border-radius: 4px;
				}
			}
		}
	}
	#tabs {
		border: 0;
		.ui-tabs-nav {
			border: inherit;
			border-radius: 0;
			background: none;
			margin: 0;
			padding: 0;
			li {

			}
		}
	}
}