.crumbs_wrap {
	margin-top: 32px;
	margin-bottom: 27px;
	.breadcrumb.my_breadcrumbs {
		background: transparent;
		margin-bottom: 0;
		color: #07af61;
		li {
			display: inline-block;
			font-weight: 300;
			font-size: 20px;
			line-height: 17px;
			font-family: $ProximaNova;
			i {
				color: inherit;
				font-weight: 100;
				font-size: inherit;
				margin-left: 10px;
				margin-right: 10px;
			}
			a {
				text-decoration: none;
				color: #07af61;
			}
			&:active {
				color: #676767;
				font-weight: 300;
				font-size: 20px;
				line-height: 17px;
			}
		}
	}
}