/* Toggle Styles */

#wrapper {
  padding-left:       0;
  -webkit-transition: all 0.5s ease;
  -moz-transition:    all 0.5s ease;
  -o-transition:      all 0.5s ease;
  transition:         all 0.5s ease;
}

#sidebar-wrapper {
  z-index:            1000;
  position:           fixed;
  left:               250px;
  width:              0;
  height:             100%;
  margin-left:        -250px;
  overflow-y:         auto;
  background:         #FDFDFD;
  -webkit-transition: all 0.5s ease;
  -moz-transition:    all 0.5s ease;
  -o-transition:      all 0.5s ease;
  transition:         all 0.5s ease;
}

#wrapper.toggled #sidebar-wrapper {
  width: 350px;
}

#page-content-wrapper {
  width:    100%;
  position: absolute;
  padding:  15px;
}

#wrapper.toggled #page-content-wrapper {
  position:     absolute;
  margin-right: -250px;
}

/* Sidebar Styles */

.sidebar-nav {
  position:   absolute;
  top:        0;
  width:      100%;
  margin:     0;
  padding:    0;
  list-style: none;
}

.sidebar-nav li {
  text-indent: 20px;
  line-height: 40px;
}

.sidebar-nav li a {
  display:         block;
  text-decoration: none;
  color:           #999999;
}

.sidebar-nav li a:hover {
  text-decoration: none;
  color:           #fff;
  background:      rgba(255, 255, 255, 0.2);
}

.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none;
}

.sidebar-nav > .sidebar-brand {
  height:      65px;
  font-size:   18px;
  line-height: 60px;
}

.sidebar-nav > .sidebar-brand a {
  color: #999999;
}

.sidebar-nav > .sidebar-brand a:hover {
  color:      #fff;
  background: none;
}

@media(min-width: 1200px) {
  #wrapper {
    padding-left: 250px;
  }

  #wrapper.toggled {
    padding-left: 0;
  }

  #sidebar-wrapper {
    width: 250px;
  }

  #wrapper.toggled #sidebar-wrapper {
    width: 0;
  }

  #page-content-wrapper {
    padding:  20px;
    position: relative;
  }

  #wrapper.toggled #page-content-wrapper {
    position:     relative;
    margin-right: 0;
  }
}

.wrapper.toggled #sidebar-wrapper {
  margin-right: 30px;
  overflow:     visible;
}

#close-sidebar-menu {
  position:      absolute;
  right:         -15px;
  top:           30px;
  cursor:        pointer;
  font-size:     30px;
  z-index:       1001;
  color:         $white_color;
  height:        30px;
  width:         30px;
  text-align:    center;
  border-radius: 50%;
  background:    $green;
  i {
    font-size:      28px;
    vertical-align: top;
    margin-left:    -2px;
  }
}

.toggled .sidebar-shadow-wr {
