.about_us_page {
  font-family: $ProximaNova;
  .about-us-content {
    img {
      width:  100%;
      height: auto;
    }
    h2 {
      margin-top:    44px;
      margin-bottom: 32px;
      color:         #393b3d;
      font-weight:   300;
      font-size:     28px;
      line-height:   17px;
    }
    .arrow-down {
      background:    url("../images/line_angle_middle.png") no-repeat center;
      width:         100%;
      height:        14px;
      margin-bottom: 40px;
    }
    p {
      color:       #393b3d;
      font-weight: 300;
      font-size:   18px;
      line-height: 40px;
      font-family: 'Merriweather', serif;
    }
  }
  section.search-tur {
    background: url('../images/we-offer-bg.png') no-repeat top center;
  }
}