@import 'datepicker.css';

.booking_page .search-form form {
	overflow: visible;
}

.countNight {
	text-align: center;
}

.no-border-right {
	border-right: 0 !important;
}

.inner-more-settings,
.product_catalog {
	.stars,
	.budget,
	.amenities,
	.type-object,
	.goal-road {
		border-right: 1px dashed #C2C4C4;
		p label {
			width: 100%;
		}
	}

	.stars,
	.budget,
	.amenities,
	.food,
	.type-object,
	.goal-road {
		color: #68696a;
		font-weight: 300;
		font-size: 14px;
		line-height: 36px;
		font-family: $ProximaNova;
		width: 100%;
		padding: 15px 14px;
		height: 164px;
		label {
			font-weight: 300;
		}
		p {
			line-height: 25px;
			border-bottom: 2px solid #F3F3F3;
			&:last-child {
				border-bottom: 0;
			}
		}
		input[type="checkbox"] {
			display: none;
		}
		input[type="checkbox"]:checked + span.checkboxx {
			display: inline-block;
			width: 18px;
			height: 18px;
			border: 1px solid #c3c5c4;
			background: url("../images/check35.png") center center no-repeat;
		}
		input[type="checkbox"] + span.checkboxx {
			vertical-align: middle;
			display: inline-block;
			width: 18px;
			height: 18px;
			border: 1px solid #c3c5c4;
			margin-right: 10px;
			float: left;
			margin-top: 5px;
		}
		p, label {
			margin-bottom: 0;
		}
	}

	.stars p {
		label {
			cursor: pointer;
			font-weight: normal;
			width: 100%;
		}
		i {
			color: #fddb93;
			&.truestar {
				color: #fdb300;
			}
		}
	}
}

.slider-range-min.ui-slider {
	height: 1px;
	border: 1px solid $green;
	margin-top: 10px;
	span {
		background: #fff;
		margin-top: -4px;
		border-radius: 50%;
		outline: none;
		border: 5px solid $green;
	}
}

.sk-form-autocomplete .more-settings .budget {
	label {
		color: #959696;
		font-weight: 300;
		font-size: 12px;
		line-height: 36px;
		font-family: $ProximaNova;
	}
	input.amount {
		width: 45px;
		height: 21px;
		padding: 0 3px;
	}
	.currency {
		float: right;
		a {
			color: #676767;
			font-weight: 300;
			font-size: 11px;
			line-height: 30px;
			font-family: inherit;
			cursor: pointer;
			padding: 0;
			&:hover,
			&:active {
				background: transparent;
			}
		}
		.dropdown-menu {
			border: 0;
			border-radius: 0;
		}
		img {
			width: 14px;
			height: 7px;
		}
	}

	.count-guests {
		padding-top: 22px;
		div {
			color: #959696;
			font-weight: 300;
			font-size: 14px;
			line-height: 24px;
			font-family: $ProximaNova;
		}
		p.adults,
		p.children {
			color: #676767;
		}
	}
	.adults {
		border-bottom: 2px solid #F3F3F3;
	}
}

.search-form {
	li.sk-form-item {
		border-left: 0 solid #fdfdfd;
		border-right: 0 solid #fdfdfd;
		border-top: 2px solid transparent;
		border-bottom: 2px solid transparent;
		&.active {
			border: 2px solid $green;
			border-radius: 6px;
			.sk-form-item-inner label {
				border-left: 0;
			}
		}
		&.active + li label {
			border-left: 0;
		}
	}
	li.sk-form-submit {
		& .sk-form-item-inner {
			height: 100%;
		}
		& a {
			vertical-align: top;
		}
	}
	.wrap-border {
		border: 2px solid $green !important;
		border-radius: 4px !important;

		& .sk-form-item-inner {
			border-radius: 4px !important;
		}
		& label {
			border-right: 0 !important;
		}
	}
	label {
		position: relative;
		input {
			width: 100%;
		}
		.sk-form-wr-img {
			position: absolute;
			margin-right: 20px;
			top: 0;
			right: 0;
			cursor: pointer;
		}
		img {
		}
	}
	position: relative;
	.sk-form-datepicker,
	.sk-form-guests {
		& input {
			cursor: pointer;
		}
	}
	li.sk-form-item:first-child {
		.sk-form-item-inner {
			label {
				border-left: 0;
			}
		}
	}
	li.sk-form-item {
		float: left;
		line-height: 72px;
		height: 72px;
		width: 100%;
		.sk-form-item-inner {
			padding-top: 11px;
			padding-bottom: 11px;
			background: #FDFDFD;
			height: 100%;
			label {
				border-left: 1px solid #A7A7A7;
				width: 100%;
				margin-bottom: 0;
				line-height: 46px;
				vertical-align: top;
				height: 46px;
				& > input {
					background: #FDFDFD;
					font-size: 22px;
					border: 0;
					vertical-align: top;
					padding-left: 17px;
					outline: none;
					height: 100%;
					font-family: $ProximaNova;
					color: #676767;
					font-weight: 100;
					line-height: 73px;
				}
				img {
					vertical-align: top;
					margin-top: 10px;
				}
			}
		}
		&.sk-form-autocomplete {
			width: 28%;
			& > label input {
				width: 100%;
				padding: 0 17px;
			}
		}
		&.sk-form-datepicker {
			width: 18%;
		}
		&.sk-form-guests {
			width: 19%;
		}
		&.sk-form-submit {
			width: 16%;
			line-height: 76px;
			height: 76px;
			.sk-form-item-inner {
				padding-top: 0;
				padding-bottom: 0;
				background: transparent;
				height: 68px;
			}
		}
	}
	.sk-form-submit .sk-form-item-inner label,
	.sk-form-guests .sk-form-item-inner label {
		border-right: 0;
	}
	li.sk-form-item {
		&.active {
			.sk-form-item-inner {
				border-radius: 4px;
			}
		}
	}
	.sk-form-autocomplete {
		.more-settings {
			height: 42px;
			padding: 0 14px;
			position: absolute;
			left: 0;
			top: 88px;
			width: 83%;
			background: rgba(0, 0, 0, 0.55);
			line-height: 42px;
			color: #fff;
			&,
			& a {
				color: #fff;
				font-size: 18px;
			}
			a {
				text-decoration: none;
			}
			a.link-hint {
				border-bottom: 1px dashed #fff;
			}

			& .hint {
				font-family: $ProximaNova;
				font-size: 18px;
				color: #fff;
				font-weight: 100;
			}
			& a.link-hint {
				font-weight: inherit;
				color: inherit;
				font-size: inherit;
				border-bottom: 1px dashed;
			}

		}
		.inner-more-settings {
			.dropdown-menu {
				padding: 0;
				width: 700px;
				left: -241px;
				h2 {
					margin: 0;
					background: #F0F0F0;
					text-align: center;
					color: #3a3b3d;
					font-size: 20px;
					line-height: 52px;
					font-family: 'proxima_novalight', sans-serif;
					border-right: 1px dashed #C2C4C4;
				}
			}
			.budget .currency .dropdown-menu {
				color: inherit;
				width: 100%;
				top: 30px;
				left: 0;
				min-width: 50px;
				li {
					width: inherit;
					padding-left: 15px;
					padding-right: 15px;
					line-height: 30px;
					height: 30px;
					&:hover {
						background: #fafafa;
					}
				}
			}
		}
	}
	.sk-form-guests .sk-form-item-inner {
		img.arrow-down {
			margin-top: 20px;
			margin-left: 10px;
		}
		.dropdown-menu {
			top: 62px;
			width: 100%;
			border-top: 0;
			z-index: 2;
			box-shadow: none;
		}
	}
	.sk-form-submit {
		.sk-form-item-inner {
			background: transparent;
			padding: 0;
		}
		margin-left: 1%;
		a.btn.btn-default {
			display: inline-block;
			background: #F58B03;
			border: 0;
			padding: 0;
			width: 100%;
			color: #ffffff;
			font-family: $ProximaNova;
			font-size: 26px;
			height: 68px;
			line-height: 42px;
			padding-top: 13px;
			padding-bottom: 13px;
			&:active {
				background: #F58a03;
			}
			i {
				vertical-align: top;
				font-size: 20px;
				margin-right: 10px;
				line-height: 46px;
			}
		}
	}

}

.booking_page,
.product_catalog {
	.more-settings {
		display: none;
	}

}

.guests {
	position: relative;
	height: 100%;
	.guests-adults,
	.guests-children {
		line-height: 40px;
		display: block;
		position: relative;
		z-index: 1;
		width: 100%;
	}
	.adults-title,
	.children-title,
	.child-age-title {
		display: inline-block;
		float: left;
		width: 40%;
		line-height: 60px;
		vertical-align: top;
		padding: 0 0 0 18px;
		cursor: pointer;
		position: relative;
	}
	.child-age-hint {
		font-size: 11px;
		color: #a7a7a7;
		position: absolute;
		bottom: 5px;
		height: auto;
		line-height: 15px;
	}
	.adults-controls,
	.children-controls,
	.child-age-controls {
		display: inline-block;
		text-align: center;
		line-height: 60px;
		height: 60px;
		width: 60%;
		vertical-align: top;
		a {
			font-size: 24px;
			font-weight: 300;
			font-family: "Times New Roman", Times, serif;
			margin: 7px;
			height: 30px;
			line-height: 30px;
			width: 30px;
			transition: background 0.2s linear;
			-webkit-transition: background 0.2s linear;
			border-radius: 50%;
			display: inline-block;
			vertical-align: middle;
			text-align: center;
			text-decoration: none;
			color: #a4a4a4;
			&:hover {
				background: #ccc;
			}
			.minus span {
				line-height: 26px;
				font-size: 0.9em;
				position: relative;
				bottom: .1em;
			}
		}
	}
	.guest-child-item {
		position: relative;
		height: 54px;
		line-height: 54px;
		background: rgba(50, 0, 0, 0.1);
		&:first-child {
			position: relative;
			&:before {
				content: '';
				position: absolute;
				top: -19px;
				right: 55px;
				border: 10px solid transparent;
				border-bottom: 9px solid rgba(50, 0, 0, 0.1);
			}
		}
		& > .child-age-title {
			height: inherit;
			line-height: 45px;
		}
		& > .child-age-controls {
			height: inherit;
			line-height: inherit;
			i {
				font-size: 12px;
				line-height: 30px;
				vertical-align: top;
			}
		}
	}
	.guests-menu {
		position: absolute;
		margin-top: -13px;
		z-index: 3;
		width: 100%;
		font-weight: 300;
		& > ul,
		& ul.children-list {
			border-right: 1px solid #ccc;
			border-bottom: 1px solid #ccc;
			border-left: 1px solid #ccc;
			background: #fff
		}
		& ul.children-list {
			margin: 4px -1px -1px -1px;
		}
		li {
			height: 100%;
			font-family: $ProximaNova;
			color: #a4a4a4;
		}
		.title,
		.controls {
			float: left;
			height: 54px;
			line-height: 54px;
		}
		.title {
			width: 40%;
			padding-left: 18px;
			font-size: 16px;
		}
		.controls {
			font-size: 20px;
			width: 60%;
			text-align: center;
			.text-field {
				height: 54px;
				line-height: 54px;
				width: 30px;
				display: inline-block;
				vertical-align: top;
			}
			button {
				outline: none;
				margin: 12px 0;
				height: 30px;
				width: 30px;
				padding: 0;
				line-height: 26px;
				border-radius: 50%;
				background: transparent;
				border: none;
				margin-left: 7px;
				margin-right: 7px;
				vertical-align: top;
				&:hover {
					background: #ccc;
				}
			}
		}
		.guest-child-item {
			width: 100%;
			line-height: 54px;
			height: 54px;
			button {
				width: 30px;
				height: 30px;
				background-size: 16px 16px;
				background-position: 7px 7px;
				background-repeat: no-repeat;
			}
		}
	}

}