body {
	font: 10pt HelveticaNeueCyr, arial, sans-serif;
	background: #f3f3f3 !important;
}

#topScroll {
	position: fixed;
	right: 100px;
	bottom: 100px;
	z-index: 99;
	border: 0;
	background-color: transparent;
	outline: none;
	line-height: 20px;
	height: 20px;
	width: 20px;
	padding: 0;
	color: #ccc;
	i {
		font-size: 30px;
		line-height: 20px;
	}
}

header {
	display: block;
	background-color: #929298;
	border-bottom: 5px solid #d7d7df;
	line-height: 66px;
	height: 66px;
	color: #fff;
	font-size: 17px;
	font-family: $ProximaNova;
}

.navbar-default .navbar-collapse, .navbar-default .navbar-form {
	border-color: #e7e7e7;
	margin-left: -15px;
}

header .navbar {
	margin-bottom: 0;
	.container-fluid {
		padding-left: 0 !important;
	}
}

header.home {
	background: #f3f3f3;
	padding-bottom: 140px;
	border-bottom: none;
	margin-bottom: 0;
	min-height: 580px;
	.top-navigation {
		margin-bottom: 230px;
		.top-nav-left {
			& > li > a:hover,
			& > li.active > a,
			li.active > a:hover {
				background-image: none;
				background-color: rgba(0, 0, 0, 0.2);
				box-shadow: none;
				border-bottom: 2px solid $orange;
			}
		}

	}
}

.top-navigation {
	font-weight: 100;
	font-size: 18px;
	font-family: $ProximaNova;
	border: 0;
	background-color: transparent;
	background-image: none;
	box-shadow: none;
	.top-nav-left {
		& > li > a,
		& > li > a:hover {
			line-height: 61px;
			padding: 0 15px;
			border: 0;
			font-family: inherit;
			font-size: inherit;
		}
		& > li > a:hover,
		& > li.active > a,
		& > li.active > a:hover {
			background-image: none;
			background-color: #07af61;
			box-shadow: none;
		}
	}
	a {
		color: #fff;
		font-size: 17px;
		line-height: 61px;
	}
	.logo {
		height: 61px;
		margin-right: 20px;
		img {
			width: 148px;
		}
	}

	.top-nav-right {
		float: right;
		height: 61px;
		line-height: 61px;
		margin-bottom: 0;
		.dropdown-menu {
			min-width: 100px;
			top: 40px;
		}
		li {
			vertical-align: top;
			font-size: 12px;
			a {
				font-size: inherit;
			}
		}
		.phone-number {
			padding-top: 12px;
			margin-left: 46px;
			margin-right: 62px;
			line-height: normal;
			font-family: $ProximaNova;
			color: #fff;
			small {
				letter-spacing: 2px;
				font-size: 12px;
				line-height: 18px;
				text-align: justify;
			}
			span {
				font-size: 22px;
				line-height: 18px;
			}
		}
		.camera {
			margin-right: 32px;
		}
		.wr-btn-enter {
			font-family: $ProximaNova;
			margin-left: 33px;
		}
		.btn-enter {
			font-size: 12px;
			line-height: 20px;
			width: 72px;
			height: 22px;
			padding: 0;
			border: 1px solid #fff;
			box-shadow: none;
			background: transparent;
			&:hover {
				color: #fff;
			}
			&:active {
				box-shadow: inset 1px 1px 1px #ccc;
				background: #8c8c8c
			}
		}
	}
}

.triangle_bottom {
	position: relative;
	&:after {
		content: '';
		position: absolute;
		bottom: -2px;
		left: 40%;
		border: 25px solid transparent;
		border-bottom: 25px solid #fff;
	}
}

.padding-left-xs {
	padding-left: 5px !important;
}

.padding-right-xs {
	padding-right: 5px !important;
}